:root {
  --color-black: #000;
  --color-white: #fff;
  --color-gray: #ccc;
  --color-blue: #4169e1;
  --color-orange: #ff5319;
  --body-color: var(--front-primary);
  --body-bg: var(--back-primary);
  --font-base: var(--font-sans);
  --font-sans:
    Lato, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Ubuntu",
    "Droid Sans", "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji",
    "Segoe UI Symbol";
  --font-serif: Georgia, "Times New Roman", Times, serif;
  --font-mono: SFMono-Regular, Menlo, Monaco, Consolas, Liberation Mono, "Courier New", monospace;
  --font-size-root: 100%;
  --font-size-base: 1rem;
  --font-weight-base: 400;
  --line-height-base: 1.25;
}

:root {
  font-size: var(--font-size-root);
}

html,
body {
  height: 100%;
}

html {
  min-width: 320px;
  min-height: 100%;
  font-size: var(--font-size-root);
  font-family: sans-serif;
  line-height: normal;
  -webkit-tap-highlight-color: rgb(var(--color-black) 0);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  color: var(--body-color);
  font-weight: var(--font-weight-base);
  font-size: var(--font-size-base);
  font-family: var(--font-base);
  line-height: var(--line-height-base);
  background-color: var(--body-bg);
}

svg {
  display: block;
}

[tabindex="-1"]:focus:not(:focus-visible) {
  outline: 0;
}

[hidden] {
  display: none;
}

a {
  color: var(--color-black);
}

.main-btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 10px 20px;
  min-height: 84px;
  min-width: 280px;
  border-radius: 10px;
  font-weight: 600;
  font-size: 33px;
  text-align: center;
  cursor: pointer;
  display: block;
  background-color: var(--color-orange);
  border: none;
  border-radius: 5px;
  width: 100%;
  min-width: 200px;
  padding: 40px 30px;
  color: var(--color-white);
  text-align: center;
}
@media screen and (min-width: 768px) {
