  border-radius: 5px;
}
.card-info__left, .card-info__right {
  padding-top: 45px;
}
.card-info__title {
  margin-bottom: 15px;
  font-size: 30px;
  font-weight: 600;
}
.card-info__item {
  margin-bottom: 20px;
}
