  font-size: 30px;
  line-height: 1;
  width: 100%;
  padding: 10px;
  min-height: 100px;
  border: 1px solid var(--color-gray);
  transition: border 0.25s ease-out, outline 0.25s ease-out;
  appearance: none;
  outline: none;
}
.input[disabled] {
